@import "../../style_variables";

.home {
  height: 100%;

  .search_results {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    transition: all 0.2s ease-in;
    z-index: 2;
  }

  .animation_container {
    width: 100%;
    height: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
